@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Roboto', sans-serif;
	overflow: hidden;
}

.wrap {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 100vh;
}

.toggle-container {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin: 10px 0;
	width: 200px;
}

.toggle {
	visibility: hidden;
}

.label {
	background-color: #d0d0d0;
	border-radius: 50px;
	width: 80px;
	height: 40px;
	display: inline-block;
	margin: 0 15px 0;
	position: relative;
	cursor: pointer;
}

.toggle:checked + .label {
	background-color: #8e44ad;
}

.ball {
	background-color: #fff;
	height: 34px;
	width: 34px;
	border-radius: 50%;
	position: absolute;
	top: 3px;
	left: 3px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-animation: slideOff 0.3s linear forwards;
	        animation: slideOff 0.3s linear forwards;
}

.toggle:checked + .label .ball {
	-webkit-animation: slideOn 0.3s linear forwards;
	        animation: slideOn 0.3s linear forwards;
}

@-webkit-keyframes slideOn {
	0% {
		-webkit-transform: translateX(0) scale(1);
		        transform: translateX(0) scale(1);
	}
	50% {
		-webkit-transform: translateX(20px) scale(1.2);
		        transform: translateX(20px) scale(1.2);
	}
	100% {
		-webkit-transform: translateX(40px) scale(1);
		        transform: translateX(40px) scale(1);
	}
}

@keyframes slideOn {
	0% {
		-webkit-transform: translateX(0) scale(1);
		        transform: translateX(0) scale(1);
	}
	50% {
		-webkit-transform: translateX(20px) scale(1.2);
		        transform: translateX(20px) scale(1.2);
	}
	100% {
		-webkit-transform: translateX(40px) scale(1);
		        transform: translateX(40px) scale(1);
	}
}

@-webkit-keyframes slideOff {
	0% {
		-webkit-transform: translateX(40px) scale(1);
		        transform: translateX(40px) scale(1);
	}
	50% {
		-webkit-transform: translateX(20px) scale(1.2);
		        transform: translateX(20px) scale(1.2);
	}
	100% {
		-webkit-transform: translateX(0) scale(1);
		        transform: translateX(0) scale(1);
	}
}

@keyframes slideOff {
	0% {
		-webkit-transform: translateX(40px) scale(1);
		        transform: translateX(40px) scale(1);
	}
	50% {
		-webkit-transform: translateX(20px) scale(1.2);
		        transform: translateX(20px) scale(1.2);
	}
	100% {
		-webkit-transform: translateX(0) scale(1);
		        transform: translateX(0) scale(1);
	}
}

